import Vue from 'vue'
import VueRouter from 'vue-router'

import Inicio from '@/views/Inicio'
import Clientes from '@/views/Clientes'
import ClientesRegistro from '@/views/ClientesRegistro'
import Login from '@/views/Login'
import Empleados from '@/views/Empleados'
import Equipos from '@/views/Equipos'
import TipoClientes from '@/views/TipoClientes'
import TipoEquipos from '@/views/TipoEquipos'
import TipoEmpleados from '@/views/TipoEmpleados'
import TipoResistencias from '@/views/TipoResistencias'
import TipoMaximoAgregados from '@/views/TipoMaximoAgregados'


Vue.use(VueRouter)

const routes = [
    {
        path:'/',
        component:Login
    },
    {
        path:'/default',
        component:Inicio
    },
    {
        path:'/Login',
        component:Login
    },
    {
        path:'/Clientes',
        component:Clientes
    },
    {
        path:'/Clientes/Registro',
        component:ClientesRegistro
    },
    {
        path:'/Empleados',
        component:Empleados
    },
    {
        path:'/Equipos',
        component:Equipos
    },
    {
        path:'/TipoClientes',
        component:TipoClientes
    },
    {
        path:'/TipoEquipos',
        component:TipoEquipos
    },
    {
        path:'/TipoEmpleados',
        component:TipoEmpleados
    },    
    {
        path:'/TipoResistencias',
        component:TipoResistencias
    },    
    {
        path:'/TipoMaximoAgregados',
        component:TipoMaximoAgregados
    }
]


const router = new VueRouter({
    routes,    
	scrollBehavior(to){
		if (to.hash) {
			return {
				selector: to.hash
			}
		}
	}
});

export default router