<template>
    <div class="container py-5">
        <h3><i class="fas fa-users"></i> Registro de clientes</h3>
        <div class="py-5">
            <b-form>
                <b-row>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-1"
                        label="Nombre"
                        label-for="txtNombre">
                            <b-form-input
                            id="txtNombre"          
                            type="text"
                            required                        
                            placeholder="Nombre(s)"
                            v-model="nombre">
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-2"
                        label="Apellido Paterno"
                        label-for="txtApellidoP">
                            <b-form-input
                            id="txtApellidoP"          
                            v-model="apellidoP"
                            type="text"
                            required
                            placeholder="Apellido Paterno" class="">
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-3"
                        label="Apellido Materno"
                        label-for="txtApellidoM">
                            <b-form-input
                            id="txtApellidoM"          
                            type="text"
                            v-model="apellidoM"
                            required
                            placeholder="Apellido Materno">
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="RFC"
                        label-for="txtRFC">
                            <b-form-input
                            id="txtRFC"          
                            type="text"
                            v-model="rfc"
                            required                        
                            placeholder="RFC...">
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="RazonSocial"
                        label-for="cboRazonSocial">
                            <b-form-select 
                                v-model="selectedRazonSocial" 
                                :options="options" 
                                id="cboRazonSocial"
                                aria-placeholder="Seleccione...">
                            </b-form-select>
                            <!-- <b-form-input
                            id="txtRazonSocial"          
                            type="text"
                            required                        
                            placeholder="Razón Social">
                            </b-form-input> -->
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Alias"
                        label-for="txtAlias">
                            <b-form-input
                            id="txtAlias"          
                            type="text"
                            v-model="alias"
                            required                                  
                            placeholder="Alias...">
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Número de teléfono"
                        label-for="txtTelefono">
                            <b-form-input
                            id="txtTelefono"                                      
                            type="text"               
                            v-model="telefono"             
                            required                                  
                            placeholder="Número de teléfono...">
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Número de celular"
                        label-for="txtCelular">
                            <b-form-input
                            id="txtCelular"          
                            type="text"
                            v-model="celular"
                            required                                  
                            placeholder="Número de celular...">
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Correo electrónico"
                        label-for="txtCorreoE">
                            <b-form-input
                            id="txtCorreoE"          
                            type="text"
                            v-model="correo"
                            required                                  
                            placeholder="Correo electrónico...">
                            </b-form-input>
                        </b-form-group>
                    </b-col>                    
                </b-row>
                <b-row>
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Crédito"
                        label-for="txtCredito">
                            <b-form-input
                            id="txtCredito"          
                            type="number"
                            v-model="credito"
                            required                                  
                            placeholder="Crédito...">
                            </b-form-input>
                        </b-form-group>
                    </b-col>      
                    <b-col class="col-md-4 col-sm-12 col-12">
                        <b-form-group
                        id="input-group-4"
                        label="Vendedores"
                        label-for="cboVendedores">
                            <b-form-select 
                                v-model="vendedorsSelects" 
                                :options="vendedores" 
                                multiple 
                                :select-size="4">
                            </b-form-select>                            
                        </b-form-group>
                    </b-col>                   
                </b-row>
                <b-row>
                    <b-col class="col-md-6 col-sm-12 col-12" align-self="end">
                        <b-button @click="guardarCliente" block variant="outline-success" class="mr-2 mt-2"><i class="fa fa-save"></i> Guardar</b-button>
                    </b-col>
                    <b-col class="col-md-6 col-sm-12 col-12" align-self="end">
                        <b-button @click="limpiar" type="reset" block variant="outline-danger" class="mt-2"><i class="fas fa-broom"></i>Limpiar</b-button>                        
                    </b-col>
                </b-row>
            </b-form>                
        </div>
    </div>
</template>
<script>
import servicio from '@/services/servicio-clientes'
import servicioAgente from '@/services/servicio-agentes'
export default{
    data(){
        return{
            selectedRazonSocial:null,
            options: [
                    { value: null, text: 'Seleccione Razón Social' },
                    { value: 1, text: 'Moral' },
                    { value: 2, text: 'Física' }                    
                ],
            nombre:'',
            nombreCompleto:'',
            apellidoP:'',
            apellidoM:'',
            rfc:'',            
            alias:'',
            telefono:'',
            celular:'',
            correo:'',
            credito:0,
            fechaCorta:'',
            vendedores:[],
            vendedorsSelects:[]
        }
    },
    mounted(){        
        this.verificaTokenValido();
        this.consultaVendedores();
    },
    methods:{
        limpiar(){
            this.nombre="";
            this.apellidoP ="";
            this.apellidoM = "";
            this.rfc ="";
            this.razonSocial ="";
            this.selectedRazonSocial = null;
            this.telefono = "";
            this.celular = "";
            this.correo = "";
            this.credito = 0;
        },
        guardarCliente(){            
            let cliente = {
                "nombre":this.nombre,
                "nombreCompleto":"",
                "apellidoP":this.apellidoP,
                "apellidoM":this.apellidoM,
                "rFC":this.rfc,
                "razonSocial":this.selectedRazonSocial,
                "alias":this.alias,
                "telefono":this.telefono,
                "celular":this.celular,
                "correo":this.correo,
                // "fechaRegistro":this.fechaRegistro,
                "credito":this.credito,                
            }
            console.log("El cliente-> ",cliente)
            servicio.guardarCliente(cliente)
            .then(r=>{                                
                if(r.value){
                    this.$swal.fire({
                        text: `El cliente ${this.nombre} se ha guardado exitosamente!`,
                        icon: "success"
                    });
                    this.limpiar();
                }
            })
        },
        consultaVendedores(){
            servicioAgente.agentesCombo()
            .then(r=> {
                console.log(r);
                if(r.value){
                    r.data.forEach(element => {
                        console.log(element)
                        let agente = {value: element.codVendedor, text:element.nombreVendedor}
                        this.vendedores.push(agente);                        
                    });
                }
            })
        }
    }
}
</script>
<style scoped>
</style>