
import servicio from '@/services/servicio-base'

const servicioEquipos = {}



servicioEquipos.equipos = ()=>{    
        return servicio.get(`/equipos/todos`)
            .then( r => {
                console.log(r);
                return r.data;
            })
            .catch( error => {
                return error;
            })    
}
export default servicioEquipos
