
import servicio from '@/services/servicio-base'

const servicioEmpleados = {}



servicioEmpleados.empleados = ()=>{    
        return servicio.get(`/empleados/todos`)
            .then( r => {
                console.log(r);
                return r.data;
            })
            .catch( error => {
                return error;
            })    
}


servicioEmpleados.tipos = ()=>{    
    return servicio.get(`/empleados/tipos`)
        .then( r => {
            console.log(r);
            return r.data;
        })
        .catch( error => {
            return error;
        })    
}
export default servicioEmpleados
