<template>
<div>
    <section class="login-container">
        <div class="login-header">
            <img src="https://concretos2h.com.mx/img/3447/818.png" alt="Logo" width="56%" class="ml-5"/>
            <hr />
        </div>
        <article class="login-body">
            <form>
                    <b-input-group class="mb-2">
                        <b-input-group-prepend is-text>
                            <i class="fa fa-user fa-lg"></i>
                        </b-input-group-prepend>
                        <b-form-input type="text" placeholder="Nombre de usuario" v-model="usuario">

                        </b-form-input>
                    </b-input-group>

                    <!-- password -->

                    <b-input-group class="mb-2 py-2">
                        <b-input-group-prepend is-text>
                            <i class="fa fa-lock fa-lg"></i>
                        </b-input-group-prepend>
                        <b-form-input type="password" placeholder="Contraseña" v-model="password">

                        </b-form-input>
                    </b-input-group>

                    <div class="py-1">
                        <button type="submit" class="btn btn-block btn-success-2h" @click="acceder">Acceder</button> 
                    </div>
                    
            </form>
        </article>
    </section>
</div>
</template>
<script>
import servicio from '@/services/servicio-base'
export default {
    data(){
        return {
            usuario:"",
            password:""
        }
    },
    methods:{
        acceder(){            
            servicio.login(this.usuario,this.password)
            .then(r=> {                
                if(r.value){
                    window.localStorage.token = `{Bearer ${r.data.accessToken}}`;
                    window.localStorage.expiresIn = r.data.expiresIn;
                    window.localStorage.refreshToken = r.data.refreshToken;
                    this.$swal.fire({
                        text: r.message,
                        icon: "success"
                    });
                    this.$router.push("default")
                }else{
                    this.$swal.fire({
                        text: r.message,
                        icon: "warning"
                    })
                }                
            })
            .catch(ex=>{
                this.$swal.fire({                    
                    text:"Problemas con el servidor",
                    icon:"error"
                },
                console.log("ha ocurrido un error",ex));
            })
        }
    },
    mounted(){
        this.verificaTokenValido();
    }
}
</script>
<style>
    body {
        background-color:whitesmoke !important; 
        background-size: cover !important;        
    }
    .card {
        background-color: rgba(255, 255, 255, 0.70);
    }
    .btn-azul-2h{
        background-color:#1B3582;
        color:whitesmoke;
    }
    .btn-azul-2h:hover{
        background-color:rgb(27, 92, 130);
        color:whitesmoke;
    }
    .btn-success-2h{
        color:white !important;
        /* background-color:#28a745 !important;         */
        background-image:linear-gradient(-180deg,#173F5F,#20639B 50%);
    }
    .btn-success-2h:hover{
        color:whitesmoke !important;
        background-color:#28a745;
        background-image:linear-gradient(-180deg,#173F5F,#20639B 90%);
    }
    .full-page{
        background-image: url("../assets/img/concretos.jpg");
         background-repeat: no-repeat;
        /* background-size: 100% 200%; */
    }
    .footer {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        background-color: gray;
        color: white;
        text-align: center;
    }
    .login-container{
        background-color: #ffffff;
        border: 1px solid #d2d2d2;
        border-radius: 8px;
        box-shadow: 0 10px 5px #888;
        height: 311px;
        margin: auto;
        overflow: auto;
        padding: 12px 12px 12px 12px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 308px;
    }
    .login-header{
        margin-top:20px;
    }
</style>