
import servicio from '@/services/servicio-base'

const servicioClientes = {}



servicioClientes.clientes = ()=>{    
        return servicio.get(`/clientes/todos`)
            .then( r => {
                console.log(r);
                return r.data;
            })
            .catch( error => {
                return error;
            })    
}

servicioClientes.guardarCliente= (cliente)=>{    
    return servicio.post("/clientes/guardar",cliente)
    .then(r=>{
        console.log(r);
        return r.data        
    })
    .catch(e=>
        {
            console.log(e);
        })
}

export default servicioClientes

