
import servicio from '@/services/servicio-base'

const servicioAgentes = {}

servicioAgentes.agentesCombo = ()=>{    
        return servicio.get(`/agentes/todosCombo`)
            .then( r => {
                return r.data;
            })
            .catch( error => {
                return error;
            })    
}

export default servicioAgentes