
const Global = {
    data(){
        return{
            mostrarHeader: false
        }
    },
    methods:{
        verificaTokenValido(){
            let token = localStorage.getItem('token');
            if(token != null)
                this.mostrarHeader = token.length > 0 ? true : false;                
            this.$emit("tokenValido")
            
        }
    }
}

export default Global;